<template>
    <nav class="navbar navigation">
        <div class="container-fluid">
            <span class="navbar-brand mb-0 h1 my-text">學員健身資料系統</span>
        </div>
    </nav>
    <div class="container align-self-stretch p-2">
        <div class="row">
            <div class="col-auto">
                <button class="btn my-accent-color-primary btn-add-member rounded-circle" @click="onAddMember"></button>
            </div>
            <div class="col my-auto">
                <input class="form-control text-start" type="text" placeholder="請輸入學員名稱或編號" v-model="searchText">
            </div>
            <div class="col-auto my-auto">
                <button class="btn my-accent-color-primary" @click="onRemoveSearchText">清除</button>
            </div>
            <div class="col-auto my-auto">
                <button class="btn my-accent-color-secondary">排序</button>
            </div>
        </div>
        <div class="row mt-2">
            <div v-if="filteredInfos.length > 0">
                <member-cell class="col-12 mb-3" v-for="info in filteredInfos" :key="info.id" v-bind:info="info" @click="onClickCell(info)" @delete-member="deleteMember(info)"/>
            </div>
            <div v-else>
                <h1>無資料</h1>
            </div>
        </div>
    </div>
    <add-member class="" title="新增學員" v-show="showAddMember" @store-member="storeMember" @close-view="showAddMember = false"></add-member>
    <alert-view title="確認" :message="'確認刪除 ' + infoToDelete.name + ' 會員？'" v-show="showAlertView" @cancel-handler="showAlertView = false" @confirm-handler="onConfirmDeleteInfo"></alert-view>
</template>

<script>
import MemberCell from '@/components/MemberCell.vue'
import AddMember from '@/components/AddMember.vue'
import AlertView from '@/components/AlertView.vue'
import FakeMemberDataJson from '@/json/fake_member_data.json'

export default {
  components: { MemberCell, AddMember, AlertView },
  data: function() {
      return {
          showAddMember: false,
          showAlertView: false,
          searchText: '',
          memberInfos: FakeMemberDataJson,
          infoToDelete: Object
      }
  },
  computed: {
      filteredInfos: function () {
          let infos = [];
          if (this.searchText) {
                for (let index in this.memberInfos) {
                    let info = this.memberInfos[index];
                        if (info.name.includes(this.searchText) || info.id == this.searchText) {
                            infos.push(info);
                        }
                }
           } else {
               infos = this.memberInfos;
           }
           return infos
      }
  },
  methods: {
      onClickCell: function(info) {
          console.log(`Selected info: ${info.name}`);
          this.$router.push({ name: 'MemberDetail' });
      },
      onAddMember: function() {
          this.showAddMember = true;
      },
      storeMember: function(info) {
          this.showAddMember = false;
          this.memberInfos.push(info);
      },
      deleteMember: function(info) {
          this.infoToDelete = info;
          this.showAlertView = true;
      },
      onRemoveSearchText: function() {
          this.searchText = '';
      },
      closeAlertView: function() {
          this.showAlertView = false;
      },
      onConfirmDeleteInfo() {
          for (let index in this.memberInfos) {
            if (this.memberInfos[index].id === this.infoToDelete.id) {
                this.memberInfos.splice(index, 1);
            }
          }
          this.closeAlertView();
          this.infoToDelete = Object;
      }
  }
}
</script>

<style>
.navigation {
    height: 100px;
    background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0) ), url('~@/assets/bg_home.jpeg') no-repeat fixed;
    background-position: center -330px;
    background-size: cover;
}
.btn-add-member {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_add_member.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
</style>