<template>
  <div class="container h-100">
        <div class="row pt-2 pb-2">
            <div class="col-auto my-auto">
                <button class="btn btn-back my-accent-color-primary rounded-circle" @click="onBack"></button>
            </div>
            <div class="col my-background-color-secondary rounded-3 my-view-shadow">
                <div class="row h-100">
                    <div class="col">
                        <div class="row row-cols-1 row-cols-sm-2 row-cols-md-2 row-cols-lg-5 h-100">
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ memberInfo.name }}
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ age }}歲
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ new Date(memberInfo.birthday).toLocaleDateString() }}
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ memberInfo.gender }}
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ memberInfo.height }}cm
                            </div>
                        </div>
                    </div>
                    <div class="col-auto my-auto">
                        <button class="btn btn-edit rounded-circle my-primary-style" @click="onEdit"></button>
                    </div>
                </div>
            </div>
            <div class="col-auto my-auto">
                <button class="btn btn-add-data my-accent-color-secondary rounded-circle" @click="onAddData"></button>
            </div>
        </div>
        <div class="row gy-2">
            <div class="col-12 col-md-7">
                <div class="my-background-color-secondary rounded-3 my-view-shadow p-2">
                    <data-view :fitnessData="fitnessData" :height="info.height" ref="dataView"/>
                </div>
            </div>
            <div class="col-12 col-md-5">
                <row class="row align-items-center h-100 my-background-color-secondary rounded-3 my-view-shadow">
                    <div class="">
                    <p class="my-text h1">暫無圖片</p>
                </div>
                </row>
                
            </div>
        </div>
  </div>
  <add-member class="" title="編輯學員" :info="info" :isAddMode="false" v-show="showAddMember" @store-member="storeMember" @close-view="showAddMember = false"></add-member>
  <add-fitness-data-view v-show="showAddFitnessData" @close-add-fitness-data="showAddFitnessData = false" @store-fitness-data="storeFitnessData"></add-fitness-data-view>
</template>

<script>
import DataView from '@/components/DataView.vue'
import AddMember from '@/components/AddMember.vue'
import AddFitnessDataView from '@/components/AddFitnessDataView.vue'
// fake data
import FakeMemberData from '@/json/fake_member_data.json'
import FakeFitnessData from '@/json/fake_fitness_data.json'

export default {
  components: { DataView, AddMember, AddFitnessDataView },
    props: {
        info: {
            type: Object,
            default: FakeMemberData[0]
        }
    },
    data: function() {
        return {
            showAddMember: false,
            showAddFitnessData: false,
            dataIndex: 0,
            memberInfo: Object,
            fitnessData: FakeFitnessData
        }
    },
    mounted: function() {
        this.memberInfo = this.info;
    },
    computed: {
        age: function() {
            var ageDifMs = Date.now() - new Date(this.memberInfo.birthday);
            var ageDate = new Date(ageDifMs); // miliseconds from epoch
            return Math.abs(ageDate.getUTCFullYear() - 1970);
        }
    },
    methods: {
        onBack: function() {
            this.$router.push('/list');
        },
        onAddData: function() {
            this.showAddFitnessData = true;
        },
        onEdit: function() {
            console.log('onEditt');
            this.showAddMember = true;
        },
        storeMember: function(info) {
            this.memberInfo.name = info.name;
            this.memberInfo.birthday = info.birthday;
            this.memberInfo.gender = info.gender;
            this.memberInfo.height = info.height;
            this.memberInfo.weight = info.weight;
            this.memberInfo.career = info.career;
            this.memberInfo.sportFrequancy = info.sportFrequancy;
            this.memberInfo.sportGoal = info.sportGoal;
            
            this.showAddMember = false;
        },
        storeFitnessData(data) {
            console.log(data);
            this.fitnessData.push(data);
            this.fitnessData.sort((a, b) => {
                return a.date > b.date ? 1 : -1
            })
            let newIndex = this.fitnessData.findIndex(d => d.date == data.date);
            this.$refs.dataView.dataIndex = newIndex;
        }
    }
}
</script>

