<template>
  <div class="dimming-view">
      <div class="container">
        <div class="align-items-center row vh-100 d-inline-flex">
            <div class="col my-background-color-primary  ps-4 pe-4 rounded-3">
                <div class="row mb-1">
                    <div class="col-auto">
                        <button class="btn my-btn-close" @click="closeAddFitnessData"></button>
                    </div>
                    <div class="col my-auto">
                        <p class="h5 my-auto">新增健身資料</p>
                    </div>
                    <div class="col-auto my-auto">
                        <button class="btn my-accent-color-primary" @click="storeFitnessData">新增</button>
                    </div>
                </div>
                <!-- input data -->
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="dateInput" class="form-label m-0 h5">測量日期</label>
                    </div>
                    <div class="col-7">
                        <input id="dateInput" class="form-control text-center" type="date" v-model="date" v-bind:class="{ 'border': hasSubmit && !date, 'border-danger': hasSubmit && !date }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="waistlineInput" class="form-label m-0 h5">腰圍度(cm)</label>
                    </div>
                    <div class="col-7">
                        <input id="waistlineInput" class="form-control text-center" type="number" min="1" v-model="waistline" v-bind:class="{ 'border': hasSubmit && !waistline, 'border-danger': hasSubmit && !waistline }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="hiplineInput" class="form-label m-0 h5">臀圍度(cm)</label>
                    </div>
                    <div class="col-7">
                        <input id="hiplineInput" class="form-control text-center" type="number" v-model="hipline" v-bind:class="{ 'border': hasSubmit && !hipline, 'border-danger': hasSubmit && !hipline }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="upperArmInput" class="form-label m-0 h5">上臂圍度(cm)</label>
                    </div>
                    <div class="col-7">
                        <input id="upperArmInput" class="form-control text-center" type="number" v-model="upperArm" v-bind:class="{ 'border': hasSubmit && !upperArm, 'border-danger': hasSubmit && !upperArm }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="thighInput" class="form-label m-0 h5">大腿圍度(cm)</label>
                    </div>
                    <div class="col-7">
                        <input id="thighInput" class="form-control text-center" type="number" v-model="thigh" v-bind:class="{ 'border': hasSubmit && !thigh, 'border-danger': hasSubmit && !thigh }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="weightInput" class="form-label m-0 h5">體重(kg)</label>
                    </div>
                    <div class="col-7">
                        <input id="weightInput" class="form-control text-center" type="number" v-model="weight" v-bind:class="{ 'border': hasSubmit && !weight, 'border-danger': hasSubmit && !weight }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="fatInput" class="form-label m-0 h5">體脂(%)</label>
                    </div>
                    <div class="col-7">
                        <input id="fatInput" class="form-control text-center" type="number" v-model="fat" v-bind:class="{ 'border': hasSubmit && !fat, 'border-danger': hasSubmit && !fat }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="muscleInput" class="form-label m-0 h5">肌肉量</label>
                    </div>
                    <div class="col-7">
                        <input id="muscleInput" class="form-control text-center" type="number" v-model="muscle" v-bind:class="{ 'border': hasSubmit && !muscle, 'border-danger': hasSubmit && !muscle }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="muscleStrengthChangeInput" class="form-label m-0 h5">肌力變化</label>
                    </div>
                    <div class="col-7">
                        <input id="muscleStrengthChangeInput" class="form-control text-center" type="number" v-model="muscleStrengthChange" v-bind:class="{ 'border': hasSubmit && !muscleStrengthChange, 'border-danger': hasSubmit && !muscleStrengthChange }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="rmInput" class="form-label m-0 h5">RM值</label>
                    </div>
                    <div class="col-7">
                        <input id="rmInput" class="form-control text-center" type="number" v-model="rm" v-bind:class="{ 'border': hasSubmit && !rm, 'border-danger': hasSubmit && !rm }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="cardiopulmonaryInput" class="form-label m-0 h5">心肺能力</label>
                    </div>
                    <div class="col-7">
                        <input id="cardiopulmonaryInput" class="form-control text-center" type="number" v-model="cardiopulmonary" v-bind:class="{ 'border': hasSubmit && !cardiopulmonary, 'border-danger': hasSubmit && !cardiopulmonary }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="triathlonInput" class="form-label m-0 h5">健力三項5RM</label>
                    </div>
                    <div class="col-7">
                        <input id="triathlonInput" class="form-control text-center" type="number" v-model="triathlon" v-bind:class="{ 'border': hasSubmit && !triathlon, 'border-danger': hasSubmit && !triathlon }">
                    </div>
                </div>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data: function() {
        return {
            hasSubmit: false,
            date: this.getTodayString(),
            weight: null,
            waistline: null,
            hipline: null,
            upperArm: null,
            thigh: null,
            muscle: null,
            fat: null,
            muscleStrengthChange: null,
            rm: null,
            cardiopulmonary: null,
            triathlon: null
        }
    },
    mounted: function() {
        // let today = new Date();
        // let todayStr = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate(); 
        // this.date = todayStr;
    },
    methods: {
        closeAddFitnessData: function() {
            this.$emit('close-add-fitness-data');
        },
        storeFitnessData: function() {
            this.hasSubmit = true;
            if (!this.date || !this.weight || !this.waistline || !this.hipline || !this.upperArm || !this.thigh || !this.muscle || !this.fat
            || !this.muscleStrengthChange || !this.rm || !this.cardiopulmonary || !this.triathlon) { return }

            console.log(this.date);
            let fitnessData = {
                date: Date.parse(this.date),
                weight: this.weight,
                waistline: this.waistline,
                hipline: this.hipline,
                upperArm: this.upperArm,
                thigh: this.thigh,
                muscle: this.muscle,
                fat: this.fat,
                muscleStrengthChange: this.muscleStrengthChange,
                rm: this.rm,
                cardiopulmonary: this.cardiopulmonary,
                triathlon: this.triathlon
            }
            console.log('date: ' + fitnessData.date);
            this.$emit('store-fitness-data', fitnessData);
            this.$emit('close-add-fitness-data');
            this.resetData();
        },
        getTodayString: function() {
            let today = new Date();
            let todayStr = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate(); 
            return todayStr
        },
        resetData: function() {
            this.hasSubmit = false;
            this.date = this.getTodayString();
            this.weight = null;
            this.waistline = null;
            this.hipline = null;
            this.upperArm = null;
            this.thigh = null;
            this.muscle = null;
            this.fat = null;
            this.muscleStrengthChange = null;
            this.rm = null;
            this.cardiopulmonary = null;
            this.triathlon = null;
        }
    }
}
</script>

