<template>
  <div class="row">
      <div class="col-auto">
          <button class="btn btn-previous" @click="onPrevious" :disabled="(dataIndex - 1) < 0"></button>
      </div>
      <div class="col my-auto">
          <p class="h5 my-text m-0">測量日期 {{ new Date(fitnessData[dataIndex].date).toLocaleDateString() }}</p>
      </div>
      <div class="col-auto">
          <button class="btn btn-next" @click="onNext" :disabled="(dataIndex + 1) >= lastIndex"></button>
      </div>
  </div>
  <div class="row ps-4 pe-4">
      <div class="col-12">
          <p class="my-second-text h4 d-inline">BMI </p>
          <p class="my-text display-1 fw-bold d-inline">{{ bmi(fitnessData[dataIndex].weight, height) }}</p>
          <i class="d-inline-block" :class="treadClass"></i>
      </div>
      <div v-for="item in fitnessDataItems" :key="item.id">
          <data-item-view 
          :icon="item.icon" 
          :title="item.name" 
          :value="fitnessData[dataIndex][item.key]" 
          :unit="item.unit" 
          :isIncreasing="isValueIncreasing(item.key)"/>
      </div>
  </div>
</template>

<script>
import DataItemView from '@/components/DataItemView.vue'
import FitnessDataItems from '@/json/fitness_data_items.json'

export default {
    components: { DataItemView },
    data: function() {
        return {
            dataIndex: 0
        }
    },
    computed: {
        treadClass: function() {
            if ((this.dataIndex - 1) >= 0) {
                let weight = this.fitnessData[this.dataIndex].weight;
                let preWeight = this.fitnessData[this.dataIndex - 1].weight;
                let curBmi = this.bmi(weight, this.height);
                let preBmi = this.bmi(preWeight, this.height);
                if (curBmi > preBmi) {
                    return "icon-trend-up";
                } else if (curBmi < preBmi) {
                    return "icon-trend-down";
                } else {
                    return "icon-trend-empty";
                }
            } else {
                return 'icon-trend-empty'
            }
        },
        lastIndex: function() {
            return this.fitnessData.length;
        }
    },
    props: {
        fitnessDataItems: {
            type: Array,
            default: FitnessDataItems
        },
        height: Number,
        fitnessData: Array,
        isIncreasing: {
            type: Boolean,
            default: true
        }
    },
    mounted: function() {
        this.dataIndex = this.lastIndex - 1;
    },
    methods: {
        onNext: function() {
            if ((this.dataIndex + 1 ) < this.lastIndex) {
                this.dataIndex++;
                console.log('index: ' + this.dataIndex);
            }
        },
        onPrevious: function() {
            if ((this.dataIndex - 1) >= 0) {
                this.dataIndex--;
                console.log('index: ' + this.dataIndex);
            }
        },
        isValueIncreasing: function(key) {
            if ((this.dataIndex - 1) >= 0) {
                let curValue = this.fitnessData[this.dataIndex][key];
                let preValue = this.fitnessData[this.dataIndex - 1][key];
                if (curValue > preValue) {
                    return true;
                } else if (curValue < preValue) {
                    return false;
                } else {
                    return null;
                }
            } else {
                return null
            }
        },
        bmi: function(weight, height) {
            if (!height && !weight) { return '---' }
            let original = weight / Math.pow(height / 100, 2);
            let rounded = Math.round(original * 10) / 10;
            return rounded.toFixed(1)
        }
    }
}
</script>

<style>
.btn-previous {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_previous.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50px;
}
.btn-next {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_next.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50px;
}
.icon-trend-up {
    height: 60px;
    width: 40px;
    background-image: url('~@/assets/icon_red_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.icon-trend-down {
    height: 60px;
    width: 40px;
    background-image: url('~@/assets/icon_green_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
    transform: scaleY(-1);
}
.icon-trend-empty {
    height: 40px;
    width: 40px;
}
</style>