<template>
  <div class="d-flex">
      <i class="icon-item align-self-center" :style="{ backgroundImage: 'url(' + iconImage + ')' }"></i>
      <p class="my-second-text fs-3 flex-fill text-start my-auto ps-2">{{ title }}</p>
      <p class="my-text fs-3 my-auto fw-bold">{{ value }} {{ unit }}</p>
      <i :class="trendClass"></i>
  </div>
</template>

<script>
export default {
    props: {
        title: String,
        value: null,
        unit: String,
        icon: String,
        isIncreasing: Boolean
    },
    computed: {
        iconImage: function() {
            let img = require('@/assets/' + this.icon);
            return img
        },
        trendClass: function() {
            if (this.isIncreasing == true) {
                return "icon-trend-red-up"
            } else if (this.isIncreasing == false) {
                return "icon-trend-green-down"
            } else {
                return "icon-trend-empty"
            }
        }
    }
}
</script>

<style>
.icon-item {
    height: 50px;
    width: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 35px;
}
.icon-trend-red-up {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_red_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
}
.icon-trend-red-down {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_red_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    transform: scaleY(-1);
}
.icon-trend-green-up {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_green_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
}
.icon-trend-green-down {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_green_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    transform: scaleY(-1);
}
.icon-trend-empty {
    height: 50px;
    width: 40px;
}
</style>